<template>
  <div id="app">
    <h1 v-text="title"></h1>
    <input v-model="newItem" @keyup.enter="addNew"/>
    <ul>
      <li v-for="item in items" v-bind:class="{finished: item.isFinished}" v-on:click="toggleFinish(item)">
        ❤ {{item.label}}
        <span v-on:click="deleteThis(item)">delete</span>
        <hr/>
      </li>
    </ul>
  </div>
</template>
<script>
import Store from './store'
export default {
  data () {
    return {
      title: 'todo list',
      items: Store.fetch() || [],
      newItem: ''
    }
  },
  methods: {
    toggleFinish: function(item) {
      item.isFinished = !item.isFinished
    },
    addNew: function() {
      this.items.push({
        label: this.newItem,
        isFinished: false
      })
      this.newItem = '';
    },
    deleteThis: function(item) {
      this.items.splice(this.items.indexOf(item), 1)
      Store.save(this.items)
    }
  }
}
</script>
<style>
*{
  margin: 0;
  padding: 0;
}
html {
  height: 100%;
}
body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
#app {
  color: #2c3e50;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
  width: 60%;
}
#app a {
  color: #42b983;
  text-decoration: none;
}
#app h1:nth-child(1) {
  line-height: 3;
  position: absolute;
  top: 10%;
}
#app input {
  width: 60%;
  line-height: 24px;
  font-size: 24px;
  position: absolute;
  top: 25%; left: 20%;
}
ul {
  position: absolute;
  top: 35%;
  text-align: left;
  width: 60%;
  height: 45%;
  overflow: auto;
}
ul li {
  list-style: none;
  line-height: 2;
  font-size: 24px;
}
span {
  font-size: 16px;
  color: #fff;
  padding: 2px 5px;
  text-align: right;
  background-color: red;
  border-radius: 5px;
}
.logo {
  width: 100px;
  height: 100px
}
.finished {
  color: #ccc;
}
hr {
  ;border-top:1px dashed #ccc;
}
p {
  text-align: left;
  position: absolute;
  bottom: 10%;
}
</style>


